import "./Footer.css"
import { useLocation } from "react-router-dom";
import { useEffect, useState } from "react";
import { connect } from "react-redux";

const Footer = ({stateScroll}) => {

    let [classFooter , setClassFooter] = useState("hide-footer")
    let [scroll , setScroll] = useState("up_scroll")

    let location = useLocation()


    useEffect(()=> {

        
        if (location.pathname === "/contact"){

            setClassFooter("show-footer")

        }else{
        
            setClassFooter("hide-footer")

        }

    },[location.pathname])

    useEffect(()=>{

        if(stateScroll === "clicked"){
            setScroll("down_scroll")
        }else{
            setScroll("up_scroll")
        }

    },[stateScroll])

    

    return (

        <footer className={classFooter}>

            <div className="container_footer">

                <div className={`${scroll} content_footer`}>

                    <div className="container_address_in_footer">
                
                        <div className="address_in_footer">
    
                            <div className="container_title">
    
                                <div className="title"> CONTACT </div>
    
                            </div>
    
                            <div className="container_text">
    
                                <div className="text">
                                    Lorem ipsum dolor sit amet consectetur adipisicing elit. 
                                    A, amet animi odio fugiat asperiores aut unde aliquid 
                                </div>
    
                            </div>
    
                            <div className="container_items">
    
                                <div className="items">
    
                                    <div className="item">
                                        <div className="address_title">Address</div>
                                        <div className="address_content">
                                            Lorem ipsum dolor sit amet consectetur
                                        </div>
                                    </div>
        
                                    <div className="item">
                                        <div className="address_title">Phone</div>
                                        <div className="address_content">
                                            +98 21 555 55 987
                                        </div>
                                    </div>
        
                                    <div className="item">
                                        <div className="address_title">E-mail</div>
                                        <div className="address_content">
                                            miladmain.mm@gmail.com
                                        </div>
                                    </div>
        
                                </div>
                        
                            </div>
    
                        </div>
    
                    </div>
    
                    <div className="container_form_in_footer">
    
                        <div className="title_form_in_footer">
    
                            <div className="title">
    
                                CO.FORM
        
                            </div>
    
                        </div>
        
                        <form action="#" class="form_in_footer">
        
                            <div className="container_inputs">
        
                                <div className="input_item">
                                    <label for="username">username</label><input id="username" type="text" autoComplete={false}/>
                                </div>
        
                                <div className="input_item">
                                <label for="user_phone">user-phone</label><input id="user_phone" type="text" autoComplete={false}/>
                                </div>
    
                            <div className="input_item">
                                    <label for="user_email">user-email</label><input id="user_email" type="text" autoComplete={false}/>
                                </div>
        
                                <div className="input_item">
                                    <label for="message">message</label><input id="message" type="text" autoComplete={false}/>
                                </div>
    
                            </div>
    
                            <div className="container_btn">
                                <button>
                                    SEND MESSAGE
                                </button>
                            </div>
    
                        </form>
                
                    </div>

                </div>
                
            </div>

        </footer>

    )
}

const mapStateToProps = state => {
    return {
        stateScroll : state.scroll
    }
}

export default connect(mapStateToProps)(Footer)